import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import store from './store'
import router from './router'
import { sync } from 'vuex-router-sync'
import VueMask from 'v-mask'

Vue.config.productionTip = false

Vue.use(VueMask, {
  placeholders: {
    N: null,
    Y: null,
  },
})

sync(store, router)

new Vue({
  vuetify,
  store,
  router,
  render: h => h(App)
}).$mount('#app')
