import Vue from 'vue'
import Router from 'vue-router'
import { trailingSlash } from '@/util/helpers'
import {
  layout,
  route,
} from '@/util/routes'



Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition

    return { x: 0, y: 0 }
  },
  routes: [
    layout('Default', [
      route('Login Screen'),

      // Pages
      route('Financial Control', null, '/financeiro'),
      route('Licences Control', null, '/licencas'),
      route('Financial ReportStaff', null, '/relatorio-financeiro'),
      route('Financial ReportAdmin', null, '/relatorio-admin'),
      route('Fleet Report', null, '/relatorio-de-frota'),
      route('Fleet Control', null, 'empresa/frota'),
      route('Home View', null, '/home'),
      route('User Register', null, 'cadastro-de-usuario'),
      route('Customer Service', null, '/pedidos'),
      route('Chunks Test', null, '/chunks'),
      route('Dashboard View', null, '/dashboard'),
      route('Tests View', null, '/tests'),

      // Components
      route('User Profile', null, '/profile'),
      route('Company Profile', null, '/perfil'),
    ]),
  ],
})

router.beforeEach((to, from, next) => {
  return to.path.endsWith('/') ? next() : next(trailingSlash(to.path))
})

export default router
