import axios from "axios"

const defaultItem = {
    IDEMPRESA: "",
    ID: "",
    MÊS: "",
    DATA: "",
    EMPRESA: "",
    CNPJ: "",
    SOLICITANTE: "TAXA",
    PAGAMENTOBOLETO: "",
    COMERCIAL: "",
    PLACA: "",
    ESTADO: "",
    STATUSAET: "SOLICITADA",
    STATUS: "TAXA PAGA",
    DATAVENCIMENTOTAXA: "",
    DATAPAGAMENTOTAXA: "",
    BANCO: "Sicredi",
    CUSTOTAXA: "",
    NUMTAXA: "",
    EMISSÃO: "",
    VENCIMENTO: "",
    AET: "",
    VALORCOBRADO: "",
    EMISSAOBOLETO: "",
    VENCIMENTOBOLETO: "",
    VALORBOLETO: "",
    NUMBOLETO: "",
    SALDO: "",
    OBSERVAÇÃO: "",
    CONTATO1: "",
    CONTATO2: "",
    EDICAO: "",
}

const statuses = [
    { value: "A VENCER", text: "A Vencer" },
    { value: "TAXA PAGA", text: "Taxa Paga" },
    { value: "CONFERIR", text: "Conferir" },
    { value: "PAGO", text: "Pago", disabled: true },
    { value: "A COBRAR", text: "A Cobrar" },
    { value: "VENCIDO", text: "Vencido" },
    { value: "NÃO COBRAR", text: "Não Cobrar" },
    { value: "A DEPOSITAR", text: "A Depositar" },
    { value: "ACERTO", text: "Acerto" },
    { value: "PRE-PEDIDO", text: "Pre-Pedido" },
]
const statusesAdmin = [
    { value: "A VENCER", text: "A Vencer" },
    { value: "TAXA PAGA", text: "Taxa Paga" },
    { value: "CONFERIR", text: "Conferir" },
    { value: "PAGO", text: "Pago" },
    { value: "A COBRAR", text: "A Cobrar" },
    { value: "VENCIDO", text: "Vencido" },
    { value: "NÃO COBRAR", text: "Não Cobrar" },
    { value: "A DEPOSITAR", text: "A Depositar" },
    { value: "ACERTO", text: "Acerto" },
    { value: "PRE-PEDIDO", text: "Pre-Pedido" },
]

var state = {
    // banners
    showError: false,
    showPost: false,
    showUpdate: false,

    // dialogs
    batchDialog: false,
    batchItems: {},
    dialogSave: false,
    selected: "",
    statusIcon: "",
    objConditionals: "",
    valid: false,

    // general data
    count: 0,
    defaultItem: defaultItem,
    editedIndex: -1,
    editedItem: defaultItem,
    fetchingLicences: false,
    licences: [],
    saving: "",
    statuses: statuses,
    statusesAdmin: statusesAdmin,
    timeNow: "",
}

const mutations = {
    showBanner (state) {
        state.showPost = !state.showPost
    },
    logTest (state) {
        console.log("oi")
        console.log(state)
    },
    fetchLicences () {
        state.licences = []
        state.fetchingLicences = true
        this.$nextTick(() => {
            return fetch("https://sheetdb.io/api/v1/afxq0nl1f0gvg")
                .then((r) => r.json())
                .then((r) => {
                    state.licences = r
                    state.fetchingLicences = false
                })
        })
    },

    setEditTime () {
        const timeElapsed = Date.now()
        const rightNow = new Date(timeElapsed)
        state.timeNow = rightNow.toLocaleString("pt-BR")
    },

    getIcon(state) {
        if (state.editedItem.STATUS === "PAGO") {
            return "mdi-check-all blue--text"
        } else if (state.editedItem.STATUS === "A VENCER" || state.editedItem.STATUS === "A DEPOSITAR") {
            return "mdi-cash-clock"
        } else if (state.editedItem.STATUS === "TAXA PAGA") {
            return "mdi-check"
        } else if (state.editedItem.STATUS === "A COBRAR") {
            return "mdi-clock-alert-outline"
        } else if (state.editedItem.STATUS === "CONFERIR") {
            return "mdi-email-alert-outline"
        } else if (state.editedItem.STATUS === "VENCIDO") {
            return "mdi-alert red--text"
        } else if (state.editedItem.STATUS === "NÃO COBRAR" || state.editedItem.STATUS === "ACERTO") {
            return "mdi-cash-remove red--text"
        } else {
            return "mdi-list-status"
        }
    },

    closeBatch (state) {
        state.count = 0
        state.selected = []
        state.batchDialog = false
        this.$nextTick(() => {
            state.batchItems = []
            state.objConditionals = {}
            state.editedItem = Object.assign({}, state.defaultItem)
            state.editedItem.STATUS = ""
            state.editedIndex = -1
            state.saving = false
        })
    },
    batchItemsUpdateConditionals (state) {
        let valorCobradoConditional = { VALORCOBRADO: `${state.editedItem.VALORCOBRADO}` }
        let numBoletoConditional = { NUMBOLETO: `${state.editedItem.NUMBOLETO}` }
        let statusConditional = { STATUS: `${state.editedItem.STATUS}` }
        let valorBoletoConditional = { VALORBOLETO: `${state.editedItem.VALORBOLETO}` }
        let emissaoBoletoConditional = { EMISSAOBOLETO: `${state.editedItem.EMISSAOBOLETO}` }
        let vencimentoBoletoConditional = { VENCIMENTOBOLETO: `${state.editedItem.VENCIMENTOBOLETO}` }
        let pagamentoBoletoConditional = { PAGAMENTOBOLETO: `${state.editedItem.PAGAMENTOBOLETO}` }
        let obsConditional = { OBSERVAÇÃO: `${state.editedItem.OBSERVAÇÃO}` }
  
        if (state.editedItem.VALORCOBRADO) Object.assign(state.objConditionals, valorCobradoConditional)
        if (state.editedItem.NUMBOLETO) Object.assign(state.objConditionals, numBoletoConditional)
        if (state.editedItem.STATUS) Object.assign(state.objConditionals, statusConditional)
        if (state.editedItem.VALORBOLETO) Object.assign(state.objConditionals, valorBoletoConditional)
        if (state.editedItem.EMISSAOBOLETO) Object.assign(state.objConditionals, emissaoBoletoConditional)
        if (state.editedItem.VENCIMENTOBOLETO) Object.assign(state.objConditionals, vencimentoBoletoConditional)
        if (state.editedItem.PAGAMENTOBOLETO) Object.assign(state.objConditionals, pagamentoBoletoConditional)
        if (state.editedItem.OBSERVAÇÃO) Object.assign(state.objConditionals, obsConditional)
    },
    updateBatchItems (state) {
        this.batchItemsUpdateConditionals()
        setTimeout(() => {
            let batchItemsIds = []
            let obj = {}

            batchItemsIds = state.selected.map((item) => item.ID)
            for (let i = 0; i < state.selected.length; i++) {
                obj = { query: `ID=${batchItemsIds[i]}` }

                Object.assign(obj, state.objConditionals)

                state.batchItems.push(obj)
            }
        }, 200)
    },
    saveBatch (state) {
        state.saving = true
        this.updateBatchItems()
        setTimeout(() => {
            this.setEditTime()
            setTimeout(() => {
                axios
                    .put(`https://sheetdb.io/api/v1/afxq0nl1f0gvg/batch_update`, {
                        data: state.batchItems, // save alterations
                    })
                    .then((response) => {
                        if (response.status === 200 || response.status === 201) {
                            state.showPost = !state.showPost
                            this.fetchLicences()
                            setTimeout(() => {
                                state.showPost = !state.showPost
                            }, 1500)
                        }
                })
                .catch((error) => {
                    if (error.response) {
                        state.showError = !state.showError
                        setTimeout(() => {
                            state.showError = !state.showError
                            window.location.reload()
                        }, 1500)
                    }
                })
                state.dialogSave = false
                setTimeout(() => {
                    this.closeBatch()
                }, 200)
            }, 200)
        })
    },
}

const actions = {
  
}

const getters = {
  
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
